@font-face {
  font-family: 'PlayfairDisplay';
  src: url(../../fonts/PlayfairDisplay-Bold.woff2), url(../../fonts/PlayfairDisplay-Bold.woff);
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}

@font-face {
  font-family: 'Raleway';
  src: url(../../fonts/Raleway-Regular.woff2), url(../../fonts/Raleway-Regular.woff);
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

@font-face {
  font-family: 'Raleway';
  src: url(../../fonts/Raleway-Bold.woff2), url(../../fonts/Raleway-Bold.woff);
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto';
  src: url(../../fonts/Roboto-Light.woff2), url(../../fonts/Roboto-Light.woff);
  font-style: normal;
  font-weight: 300;
  font-display: swap;
}

:root {
  --color-black: #000;
  --color-white: #fff;
  --color-accent: #d7b049;
  --color-dark: #242b33;
  --quill-gray: #bebebe;
  --darkgrayish-blue: #2d343c;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: var(--color-white);
  background-color: var(--color-dark);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

.site-container {
  overflow: hidden;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  border: 0 !important;
}

.btn-reset {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border-style: none;
}

.container {
  max-width: 1110px;
  padding: 0 30px;
  margin: 0 auto;
}

/* components styles */
.header {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(180deg, #242b33 11.98%, rgba(45, 52, 60, 0.38) 51.04%, #242b33 92.19%), url(../../img/header.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.header__nav {
  margin: 20px 0;
}

.header__nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__main {
  height: 100%;
}

.header__main-container {
  display: flex;
  flex-direction: column;
  justify-content: end;
  height: 100%;
}

.header__content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  max-width: 582px;
  height: 70vh;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}

.header__content h1 {
  margin-bottom: 30px;
  font-family: 'PlayfairDisplay', sans-serif;
  font-size: 48px;
  font-weight: 700;
  color: var(--color-accent);
}

.header__mouse {
  position: absolute;
  top: 250px;
  left: 50%;
  transform: translateX(-50%);
}

.header__mouse-icon {
  width: 40px;
  height: 40px;
  animation: down-up 1s cubic-bezier(0.1, 0.25, 0.1, 1) 0s infinite alternate both;
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo__img {
  max-width: 70px;
}

.logo__name {
  color: var(--color-accent);
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 40px;
}

.contacts {
  display: flex;
  justify-content: space-around;
  margin-top: 20%;
}

.contacts__icon {
  max-width: 21px;
  max-height: 21px;
  fill: var(--color-white);
}

.contacts__link, .contacts__icon {
  transition: all .5s ease-out;
}

.contacts__link:hover {
  color: var(--color-accent);
}

.contacts__link:hover svg {
  fill: var(--color-accent);
}

@keyframes down-up {
  from {
    transform: translateY(-5px);
  }
  to {
    transform: translateY(3px);
  }
}

.gallery {
  position: relative;
  z-index: 100;
  margin: 7% 0;
}

.gallery__title {
  font-family: 'PlayfairDisplay', sans-serif;
  font-size: 24px;
  font-weight: 700;
  color: var(--color-accent);
  text-align: center;
  margin-bottom: 40px;
}

.gallery__body {
  position: relative;
}

@media (any-hover: none) {
  .gallery__body {
    overflow: auto;
  }
}

.gallery__items {
  display: grid;
  grid-template-columns: auto;
  grid-auto-flow: column;
  align-items: center;
}

@media (any-hover: hover) {
  .gallery__items {
    justify-content: center;
  }
}

.gallery__column {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.gallery__column:first-child {
  justify-content: flex-end;
}

.gallery__row {
  display: inline-grid;
  padding: 0 8px;
  grid-gap: 20px;
  grid-template-columns: auto;
  grid-auto-flow: column;
}

.gallery__row:not(:last-child) {
  margin: 0 0 16px 0;
}

.row-gallery_left-top {
  align-items: end;
  justify-content: end;
}

.row-gallery_left-bottom {
  align-items: start;
  justify-content: end;
}

.row-gallery_center {
  display: block;
}

.row-gallery_right-top {
  align-items: end;
}

.row-gallery_right-bottom {
  align-items: start;
}

.row-gallery__item img {
  border-radius: 5px;
  transition: all 0.3s ease;
}

.row-gallery__item:hover img {
  transform: scale(1.1);
}
